import Card from "./Card";

const GamePanel = (props) => {
  return (
    <div className="game-panel">
      {props.complete ? (
        <div className="subject-title">You've completed the round!</div>
      ) : props.related ? (
        <div className="subject-title">
          {" "}
          Click on the {props.remaining}{" "}
          {props.remaining === 1 ? "word" : "words"} related to{" "}
          <strong>{props.subject}</strong>
        </div>
      ) : (
        <div className="subject-title">
          Click to eliminate the {props.remaining}{" "}
          {props.remaining === 1 ? "word" : "words"}{" "}
          <div className="underline">not</div> related to:{" "}
          <strong className="subject">{props.subject}</strong>
        </div>
      )}
      <div className="card-display">
        {props.answers.map((val, index) => (
          <Card
            related={val.related}
            onClick={props.onClick}
            key={index}
            word={val.word}
            resetCards={props.resetCards}
            complete={props.complete}
          />
        ))}
      </div>
    </div>
  );
};

export default GamePanel;
