const StartModal = (props) => {
  return (
    <div className="start-modal">
      <h1 className="start-modal-header">You are the word eliminator!</h1>
      <div className="instructions">
        <p className="instruction">
          <strong>1</strong>. A subject word will be shown during each round
        </p>
        <p className="instruction">
          <strong>2</strong>. Scan all the available words to find the three
          words that are directly related to the subject word
        </p>
        <p className="instruction">
          <strong>3</strong>. Click all of the unrelated words to eliminate
          them!
        </p>
      </div>
      <button onClick={props.onClick} className="start-modal-btn">
        Start
      </button>
    </div>
  );
};

export default StartModal;
