import { useEffect, useRef } from "react";
import tick from "../img/tick.svg";

const Card = (props) => {
  const cardRef = useRef();

  useEffect(() => {
    if (props.resetCards) {
      cardRef.current.className = "card";
      cardRef.current.style.pointerEvents = "";
    }

    if (props.complete) {
      cardRef.current.className += " fade";
    }
  });

  return (
    <div
      ref={cardRef}
      className="card"
      id={!props.related ? "unrelated" : "related"}
      onClick={props.onClick}
    >
      <p className="answer">{props.word}</p>
      <img className="tick" src={tick} alt="tick" />
    </div>
  );
};

export default Card;
