import "./App.css";
import { useState, useEffect, useRef } from "react";
import subjects from "./subjects";
import StartModal from "./components/StartModal";
import GamePanel from "./components/GamePanel";
import glass from "./img/glass.png";
import thumbsUp from "./img/thumbsUp.png";
import confetti from "canvas-confetti";

const numbers = [1, 2, 3, 4];

const random = (arr) => {
  let randomNum = Math.floor(Math.random() * arr.length);
  return arr[randomNum];
};

const chooseSubjects = () => {
  let main = random(subjects);
  let chosenSubjects = [];
  let subjectNames = [];
  chosenSubjects.push(main);
  subjectNames.push(main.name);

  for (let i = 0; i < 6; i++) {
    let sub = random(subjects);

    while (
      sub.name === main.name ||
      subjectNames.includes(sub.name) ||
      main.avoidSubjects.includes(sub.name)
    ) {
      sub = random(subjects);
    }

    chosenSubjects.push(sub);
    subjectNames.push(sub.name);
  }

  return chosenSubjects;
};

const fetchWord = async (subject, existingWords) => {
  let wordList = subject.words;
  let chosenWord = random(wordList);

  while (
    subject.disallowed.includes(chosenWord) ||
    existingWords.includes(chosenWord)
  ) {
    chosenWord = random(wordList);
  }

  return chosenWord;
};

const buildWordState = async (chosenSubjects) => {
  let wordsArray = [];
  let chosenWords = [];

  for (let i = 0; i < 3; i++) {
    let sub = chosenSubjects[0];
    let word = await fetchWord(sub, chosenWords);
    wordsArray.push({ word, related: true });
    chosenWords.push(word);
  }

  for (let i = 1; i < 7; i++) {
    let sub = chosenSubjects[i];
    let word = await fetchWord(sub, chosenWords);
    wordsArray.push({ word, related: false });
    chosenWords.push(word);
  }

  return wordsArray;
};

const launchConfetti = (msg = "") => {
  const time = msg === "end" ? 12 : 1.5;

  let end = Date.now() + time * 1000;

  const frame = () => {
    confetti({
      particleCount: 3,
      angle: 60,
      spread: 255,
      origin: { x: 0, y: 1 },
    });
    confetti({
      particleCount: 3,
      angle: 120,
      spread: 255,
      origin: { x: 1, y: 1 },
    });

    if (Date.now() < end) {
      requestAnimationFrame(frame);
    }
  };

  frame();
};

const snowConfetti = () => {
  var duration = 12 * 1000;
  var animationEnd = Date.now() + duration;
  var skew = 1;

  function randomInRange(min, max) {
    return Math.random() * (max - min) + min;
  }

  const frame = () => {
    var timeLeft = animationEnd - Date.now();
    var ticks = Math.max(200, 500 * (timeLeft / duration));
    skew = Math.max(0.8, skew - 0.001);

    confetti({
      particleCount: 1,
      startVelocity: 50,
      ticks: ticks,
      decay: 0.1,
      origin: {
        x: Math.random(),
        // since particles fall down, skew start toward the top
        y: Math.random() * skew - 0.3,
      },
      colors: ["#26ccff", "#a25afd", "#ff5e7e"],
      shapes: ["square"],
      gravity: randomInRange(0.4, 0.6),
      scalar: randomInRange(0.4, 1),
      drift: randomInRange(0.2, 0.4),
    });

    if (timeLeft > 0) {
      requestAnimationFrame(frame);
    }
  };

  frame();
};

const App = () => {
  const [wordsState, setWordsState] = useState([]);
  const [subjectState, setSubjectState] = useState({});
  const [reload, setReload] = useState(true);
  const [readyToRun, setReadyToRun] = useState(false);
  const [complete, setComplete] = useState(false);
  const [count, setCount] = useState(0);
  const [resetCards, setResetCards] = useState(false);
  const [countLimit, setCountLimit] = useState(6);
  const [related, setRelated] = useState(false);
  const [remaining, setRemaining] = useState(6);
  const [progressPercent, setProgressPercent] = useState(0);
  const [gameComplete, setGameComplete] = useState(false);

  const progressRef = useRef();

  const startClick = () => {
    setReadyToRun(true);
  };

  const cardClick = (e) => {
    let related = e.target.id === "related";

    if (!related) {
      e.target.className += " spin";
      e.target.style.pointerEvents = "none";
      let currentCount = count + 1;

      setRemaining((prevState) => {
        return prevState - 1;
      });

      if (currentCount === countLimit) {
        setComplete(true);

        let prog = progressPercent;
        setProgressPercent((prev) => {
          return prev + 5;
        });
        progressRef.current.style.height = `${100 - (prog + 5)}%`;

        if (prog + 5 > 99) {
          setGameComplete(true);
        }
      } else {
        setCount(currentCount);
      }
    } else {
      e.target.className += " wobble";
      setTimeout(() => {
        e.target.className = "card";
      }, 600);
    }
  };

  const continueClick = () => {
    setReload(true);
    setResetCards(true);
    setComplete(false);
  };

  useEffect(() => {
    if (complete) {
      launchConfetti();
    }

    if (gameComplete) {
      launchConfetti("end");
      snowConfetti();
    }

    if (reload) {
      let randomNumber = random(numbers);

      if (randomNumber < 5) {
        setCountLimit(6);
        setRelated(false);
        setRemaining(6);
      } else {
        setCountLimit(3);
        setRelated(true);
        setRemaining(3);
      }

      const createWordsState = async () => {
        let allSubjects = chooseSubjects();
        setSubjectState(allSubjects[0]);
        let wordsArray = await buildWordState(allSubjects);
        wordsArray.sort((a, b) => {
          return Math.random() - 0.5;
        });

        if (randomNumber > 4) {
          wordsArray.forEach((val) => {
            val.related = !val.related;
          });
        }
        setWordsState(wordsArray);
        setReload(false);
        setResetCards(false);
        setCount(0);
      };
      createWordsState();
    }
  }, [reload, complete, gameComplete]);

  let mainDisplay = !readyToRun ? (
    <StartModal onClick={startClick} className="start-modal" />
  ) : (
    <GamePanel
      onClick={cardClick}
      answers={wordsState}
      remaining={remaining}
      related={related}
      subject={subjectState.name}
      resetCards={resetCards}
      complete={complete}
    />
  );

  const positiveFeedbackMessages = [
    "Good job!",
    "Well done!",
    "Fantastic stuff!",
    "Bravo!",
    "Excellent work!",
    "You're a pro at this!",
    "You're doing great!",
  ];

  let progressSection = readyToRun && (
    <div className="progress-section">
      <div className="progress-title">Progress</div>
      <div className="line twenty-five"></div>
      <div className="line fifty"></div>
      <div className="line seventy-five"></div>
      <div className="progress-bar">
        <div ref={progressRef} className="progress"></div>
      </div>
    </div>
  );

  return (
    <div className="App">
      <div className="title">
        <img className="glass" src={glass} alt="magnifying glass icon" />
        <p>Word Eliminator</p>
      </div>
      {!gameComplete && mainDisplay}
      {progressSection}
      {!gameComplete && complete && (
        <div className="complete-modal">
          <p className="complete-msg">
            {random(positiveFeedbackMessages)}
            <img className="thumbs-up fade-in" alt="thumbs up" src={thumbsUp} />
          </p>
        </div>
      )}
      {gameComplete && (
        <div className="complete-modal game-complete-modal">
          <p className="complete-msg game-complete-msg">
            <div className="game-complete-msg-container">
              <p className="underline">CONGRATULATIONS!</p>
              <p className="game-complete-msg-line">
                You eliminated all the right words!
              </p>
              <p>Great job!</p>
            </div>
          </p>
          <img
            className="thumbs-up fade-in thumbs-up-complete"
            alt="thumbs up"
            src={thumbsUp}
          />
        </div>
      )}
      {!gameComplete && complete && (
        <button onClick={continueClick} className="continue-btn">
          Continue
        </button>
      )}
    </div>
  );
};

export default App;
